import Vue from 'vue'
import VueRouter from 'vue-router'
const Login = () =>  import(/* webpackChunkName: "login_home_welcome" */ '../components/Login')
const Home = () =>  import(/* webpackChunkName: "login_home_welcome" */ '../components/Home')
const Welcome = () =>  import(/* webpackChunkName: "login_home_welcome" */ '../components/Welcome')

// // 注册用户列表组件
// import Users from '../components/user/Users'
const Users = () =>  import(/* webpackChunkName: "Users_Rights_Roles" */ '../components/user/Users.vue')
// // 注册权限组件
// import Rights from '../components/power/Rights'
const Rights = () =>  import(/* webpackChunkName: "Users_Rights_Roles" */ '../components/power/Rights.vue')

// // 注册用户管理组件
// import Roles from '../components/power/Roles'
const Roles = () =>  import(/* webpackChunkName: "Users_Rights_Roles" */ '../components/power/Roles.vue')



// // 注册商品分类组件
// import Cate from '../components/good/Cate'
const Cate = () =>  import(/* webpackChunkName: "Cate_Params_List_Add" */ '../components/good/Cate.vue')

// // 注册分类参数组件
// import Params from '../components/good/Params'
const Params = () =>  import(/* webpackChunkName: "Cate_Params_List_Add" */ '../components/good/Params.vue')

// // 注册商品列表组件
// import List from '../components/good/List.vue'
const List = () =>  import(/* webpackChunkName: "Cate_Params_List_Add" */ '../components/good/List.vue')

// // 注册商品列表组件
// import Add from '../components/good/add.vue'
const Add = () =>  import(/* webpackChunkName: "Cate_Params_List_Add" */ '../components/good/add.vue')


// 注册订单列表组件
// import Order from '../components/order/Order.vue'
const Order = () =>  import(/* webpackChunkName: "order_report" */ '../components/order/Order.vue')

// 注册订单列表组件
// import Report from '../components/report/report.vue'
const Report = () =>  import(/* webpackChunkName: "order_report" */ '../components/report/report.vue')






Vue.use(VueRouter)

const routes = [
  {
    path:"/",
    redirect:"/login"
  },
 {
  path:"/login",
  component:Login
 }, {
  path:"/home",
  component:Home,
  // Welcome 子组件，进入就显示Welcome页面 需要路由重定向 在Home中需要占位符
  redirect:'/welcome',
  children:[
  // welcome 和users等一系列子组件都在 原先页面的基础上 在el-main中显示 so都在children中
    {path:'/welcome',component:Welcome,},
    // 用户列表
    {path:'/users',component:Users,},
    // 权限管理
    {path:"/rights",component:Rights,},
    // 角色列表
    {path:"/roles",component:Roles,},
    // 商品分类
    {path:"/categories",component:Cate,},
    // 商品分类
    {path:"/params",component:Params,},
     // 商品列表
    {path:"/goods",component:List,},
      // 商品添加
    {path:"/goods/add",component:Add,},
      // 订单列表
    {path:"/orders",component:Order,},
      // 数据报表
      {path:"/reports",component:Report,},
  ]
 }
 
]

const router = new VueRouter({
  routes
})
// 挂载路由导航守卫 参数为函数
router.beforeEach((to,from,next)=>{
  // to 去哪 from 哪来  next() "()"没地址表示放行 带地址表示强制去
  if(to.path==='/login') return next()
  const tokenStr=sessionStorage.getItem('token')  
  if(!tokenStr) return next('/login')
  next()
})

export default router
